.popup__container {
  padding: 34px 36px 37px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  max-width: 430px;
  width: 100%;
  position: relative;
}

@media (max-width: 576px) {
  .popup__container {
    padding: 25px 22px;
    max-width: 282px;
  }
}

